body {
  background-image: url(https://www.minecraft.net/etc.clientlibs/minecraft/clientlibs/main/resources/img/feature-content/bg-wool-light.png);
  background-image: url(background.png);
  background-repeat: repeat;
  height: 100vh;

}

.fade-enter{
   opacity: 0;
}
.fade-exit{
   opacity: 1;
}
.fade-enter-active{
   opacity: 1;
}
.fade-exit-active{
   opacity: 0;
}
.fade-enter-active,
.fade-exit-active{
   transition: opacity 500ms;
}


.response-text {
  padding: 2vh;
}

.grids {
  justify-content: center;
  display: flex;
}

#grid {
  width: 60vh;
  padding: 2vh;
}

.grid-element {
  background-color: #8b8b8b;
  border-top: thick double #373737;
  border-left: thick double #373737;
  border-right: double #FFFFFF;
  border-bottom: double #FFFFFF;
  float: left;
  width: 17vh;
  height: 17vh;

}

img.displayed {
  display: block;
  margin: auto;
  padding: 1vh;
  width: 15vh;
}

#buttons {
  text-align: right;
  padding: 1vh;
}

#buttons > button {
 margin-right: 1vh;
 font-size: 2vh;
}


@media screen and (orientation: portrait) {
  #grid {
    width: 60vw;
    padding: 2vw;
    padding-top: 5vw;
  }

  .grid-element {
    width: 17vw;
    height: 17vw;

  }

  img.displayed {
    padding: 1vw;
    width: 15vw;
  }

  #buttons {
    padding: 1vw;
  }

  #buttons > button {
   margin-right: 1vw;
   font-size: 2vw;
  }
}

